.zoom-in-zoom-out {
    animation: zoom-in-zoom-out 30s linear 1;
}

@keyframes zoom-in-zoom-out {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(1.25);
    }
}

.zoom-ended {
    transform: scale(1.25);
}

.animationProgress {
    animation: animationProgress 30s linear infinite;
}

@keyframes animationProgress {
    0% {
        width: 0%;
    }
    100% {
        width: 100%;
    }
}

